import React from 'react'
import {Helmet} from "react-helmet";
import PageTransition from 'gatsby-plugin-page-transitions'
import { Link } from "gatsby"
import Header from '../components/header'
import Cta from '../components/Cta';
import Footer from '../components/footer'
import lexicrea_img_motivations from '../images/lexicrea_img_motivations.png';



let pathPrefix = `/`



export default () => (
    <PageTransition>
    <div className="layoutblocksContainer">
        <Helmet>
            <meta charSet="utf-8" />
            <title>Lexique de la Créativité - Cerveau, Curiosité et Créativité</title>
            <meta name="description" content="Plongez dans l'exploration des liens entre le cerveau, la curiosité et la créativité, et découvrez comment ces aspects influencent la pensée créative." />
            <link rel="canonical" href="https://www.lexiquedelacreativite.com" />
        </Helmet>

        <div className="layoutblocksheaderSidebar">
            <Header/>
        </div>

        <div className="layoutblocksbodyMain">
        <Cta/>
            <div className="section">

            <div className="row align-center padding5_top">
                    <div className="small-11 medium-8 columns padding5_top">
                        <p className="letterCapitalcontent">C</p>
                        <h2>Cerveau</h2>
                        <p className="text-left content-project">Les études sur les mécanismes cérébraux de la créativité sont récentes. Ce système vivant extraordinairement complexe cache encore de nombreux secrets. Sa plasticité et son adaptation permanente ne sont plus des hypothèses : il a la capacité de renouveler une partie des cellules qui le composent. Plus nous le stimulons, plus nous l’entraînons, plus notre cerveau évolue. Attardons-nous un peu sur les parties de son anatomie impliquées dans la créativité.
                        </p>
                        <h4>Le lobe frontal</h4>
                        <p className="text-left content-project">Le lobe frontal se divise en trois parties : le cortex moteur, le cortex prémoteur et le cortex préfrontal.</p>
                        <h4>Le cortex préfrontal</h4>
                        <p className="text-left content-project">Cette partie du lobe frontal - située derrière les yeux - héberge des caractéristiques uniques à notre espèce : conscience, personnalité, morale et... créativité. C'est la dernière partie de notre cerveau qui se développe, et sa fonction en fait un acteur majeur du processus créatif. Son rôle dans le processus créatif a été découvert à la suite de l’observation d’une perte de créativité chez des patients ayant des lésions au niveau du cortex préfrontal (Chow and Cumming). Les chercheurs ont aussi observé une évolution du niveau de créativité visuelle chez une femme qui a développé une démence du cortex préfrontal (Bruce Miller). Leur théorie est que la démence a permis de libérer le cortex préfrontal du contrôle des zones de langage de l’hémisphère gauche.</p>
                        <h4>Les hémisphères</h4>
                        <p className="text-left content-project">
                            Le rôle des hémisphères de notre cerveau dans la créativité est un thème largement abordé. La croyance populaire voudrait que l’hémisphère droit soit le moteur de la créativité. L'hémisphère gauche serait celui de la logique et n'interviendrait pas dans le processus créatif. Cette séduisante conclusion a été remise en question par quelques récentes études. En réalité, il semble que la créativité serait le fruit d'un dialogue entre les deux hémisphères et d’un réseau de connections neuronales très développé. Attardons-nous un peu sur le rôle de chacun des hémisphères, tels qu'ils ont été présentés au cours des 60 dernières années.
                        </p>
                        <h5>L’hémisphère gauche</h5>
                        <p className="text-left content-project">
                            L’hémisphère gauche enregistre une augmentation de son activité lors de la gestion de tâche analytiques, répétitives qui requierent de la concentration.
                        </p>
                        <h5>Hémisphère droit</h5>
                        <p className="text-left content-project">
                            Les fonctions localisées dans l'hémisphère droit sont : (i) la vision et l'imagination visuelle et auditive, ce qui se traduit par une reconnaissance des idéogrammes et la capacité d'abstraction (construction de figures géométriques, par ex) et par conséquent une meilleure aptitude pour les arts en général et la musique en particulier (Dr.Rubia).
                        </p>
                        <p className="text-left content-project">
                            Le Dr. Nóvoa Santos affirme qu’il existe chez une grande majorité de personne une asymétrie fonctionnelle du cerveau, ce qui se traduit par la prédominance d'un des deux hémisphères. Notre civilisation est souvent surnommée "civilisation du cerveau gauche".
                        </p>
                        <p className="text-left content-project">
                            Le psychologue Gooch distinguait bien le "rationnel" de l'hémisphère gauche, 'l'arrationnel" de l'hémisphère droit et "l'irrationnel" du cervelet. Ces précisions sont dans la lignée platonicienne selon laquelle "l'âme" (la psyché) serait tripartite, idée aussi partagée par l'école de Pythagore.
                        </p>
                        <p className="text-left content-project">
                            La créativité pourrait reposer sur un dialogue sain entre les deux hémisphères, avec une prédominance de l'hémisphère droit, plus sensible et synthétique lors du processus créatif. L’hémisphère gauche pourrait être impliqué dans la prise de décision and dans la planification de la tâche à réaliser. Un déséquilibre dans ce dialogue entre les deux hémisphères peut être une piste à étudier pour comprendre pourquoi certains créateurs ont eu des épisodes de troubles mentaux dans leurs vies. Ou encore pourquoi certains créateurs sont incapables de donner vie à leurs idées.
                        </p>
                        <p className="text-left content-project">
                            De récentes études ont mis en lumière le fait que le cerveau créatif serait doté de circuits neuronaux plus complexes et plus connectés qu’un cerveau non-créatif. Les synesthésies évoquées par de nombreux créatifs pourraient être le reflet de cette hyper-connectivité plaçant encore une fois les associations sous toutes leurs formes au cœur de la créativité.
                        </p>
                        <p className="text-left content-project">
                            Ces découvertes posent une autre question : les mécanismes cérébraux liés à la créativité sont-ils innés ou apparaissent-ils en pratiquant ? Le cerveau a encore de nombreux secrets à dévoiler…
                        </p>
                        <h4>Dopamine, serotonine et DRD2</h4>
                        <p className="text-left content-project">Le goût pour la découverte et l'exploration, l'intrépidité, la curiosité, l'éveil du désir pour de nouvelles expériences (physiologiques mais aussi spirituelles), mais aussi l'élan vers la recherche de nouvelles informations sont aussi liés à la dopamine, selon des travaux récents.
                        Ce neurotransmetteur (une substance chimique qui permet la transmission de signaux entre les cellules du cerveau, les neurones en liant les récepteurs) est impliqué dans le contrôle des émotions, le plaisir, la curiosité, les mouvements et le contrôle des flux d'information. La dopamine est souvent surnommée "l'hormone de récompense" car elle intervient dans la reconnaissance inconsciente de ce qui pourrait nous mener à une récompense. Elle est donc sécrétée pour nous inciter à répéter une expérience qui a été jugée riche en récompense pour l'organisme.</p>
                        <p className="text-left content-project">La maladie de Parkinson se caractérise par une baisse du taux de dopamine. Il a été observé que les patients à qui étaient administrés des médicaments augmentant le taux de dopamine avaient un intérêt grandissant envers l'environnement qui les entoure et les activités artistiques. Hans Eysenck suggère que les différents ratios sérotonine/dopamine expliquent la divergence des comportements cognitifs et que seul le ratio moyen fort permet de créer.</p>
                        <h5>DRD2</h5>
                        <p className="text-left content-project">Les DRD2 sont les transporteurs de dopamine (neurotransmetteur impliqué dans le contrôle des émotions, le plaisir, la curiosité, les mouvements et le contrôle des flux d'information).</p>
                        <p className="text-left content-project">À la suite d’un mécanisme dans lequel interviennent le thalamus (zone cérébrale relais entre les sens et la réflexion) et la molécule DRD2 qui transporte la dopamine, les neurones du cortex pré-frontal sont inhibées et filtrent les informations extérieures.</p>
                        <p className="text-left content-project">Si le taux de molécule DRD2 est faible, alors moins de neurones liés avec l'extérieur sont inhibées et il est probable que le rôle de filtre du thalamus s'en trouve diminué, ce qui a pour conséquence une plus grande réceptivité du cerveau face à l'environnement extérieur.</p>
                        <p className="text-left content-project">Le sujet est alors plus sensible à ce qu'il voit, touche et ressent. De la même manière, les idées et les connexions divergentes affluent d'avantage que chez les personnes disposant d'un taux plus élevé de récepteurs D2. Des études récentes ont démontré que les individus créatifs, tout comme les schizophrènes ont un taux de DRD2 plus faible que la majorité.</p>
                        <img className="articleIllustration" src={lexicrea_img_motivations} alt="motivations de la créativité" />

                        <p className="letterCapitalcontent">C</p>





                        <h2>Contemplation et Curiosité</h2>
                        <p className="text-left content-project"></p>
                        <h2 className="project-quote">« L´esprit est comme un parachute – il ne fonctionne bien que s´il est ouvert. »
                        <p className="project-quote-footer">Thomas Dewar</p></h2>
                        <p className="text-left content-project">Nous vivons dans une société de la spécialisation. La lecture aujourd’hui ressemble plus à l’extraction minière d’information venant consolider un savoir précis qu’à la contemplation. A la fin des années 90, une des promesses nées de l’engouement pour internet était l’accès à un contenu riche et varié. Désormais, les algorithmes nous proposent du contenu basé sur nos recherches précédentes, nous enfermant dans un tunnel. Certaines plateformes commencent à proposer des résultats de manière aléatoire afin de découvrir d’autres contenus mais le résultat est encore plutôt décevant. Pourtant, notre cerveau travaille lui aussi comme un ordinateur : plus les entrées (inputs) sont riches et variées, plus les sorties (outputs) le seront aussi ! No input, no output disait Joe Strummer (The Clash).
                        La curiosité est la base d’une créativité prolifique. Sortir du tunnel de l’information et de sa zone de confort pour laisser plus de place à la contemplation est un travail qui requiert beaucoup d’effort mais les effets sont immédiats et gratifiants.
                        </p>
                    </div>
                </div>

            </div>
            <p className="forward">
            <Link to="/D_livre_lexique_creativite_drogues_et_creativite/">
                <p className="letterCapital">D</p>
                <p>Drogues</p>
                <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-arrow-narrow-right" width="44" height="44" viewBox="0 0 24 24" stroke-width="1" stroke="#282524" fill="none" stroke-linecap="round" stroke-linejoin="round">
                    <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                    <path d="M5 12l14 0" />
                    <path d="M15 16l4 -4" />
                    <path d="M15 8l4 4" />
                </svg>
            </Link>


        </p>
        </div>

    </div>






</PageTransition>
)

